@charset "UTF-8";
@font-face {
  font-family: "IBM Plex Mono";
  font-weight: 400;
  font-style: normal;
  font-display: block;
  src: url("./fonts/IBMPlexMono-Regular.woff") format("woff");
}
@font-face {
  font-family: "IBM Plex Mono";
  font-weight: 700;
  font-style: normal;
  font-display: block;
  src: url("./fonts/IBMPlexMono-Bold.woff") format("woff");
}
@font-face {
  font-family: "IBM Plex Sans";
  font-weight: 400;
  font-style: normal;
  font-display: block;
  src: url("./fonts/IBMPlexSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "IBM Plex Sans";
  font-weight: 700;
  font-style: normal;
  font-display: block;
  src: url("./fonts/IBMPlexSans-Bold.woff") format("woff");
}
@font-face {
  font-family: "Playfair Display";
  font-weight: 400;
  font-style: normal;
  font-display: block;
  src: url("./fonts/PlayfairDisplay-VariableFont_wght.woff") format("woff");
}
@font-face {
  font-family: "Ysabeau SC";
  font-weight: 400;
  font-style: normal;
  font-display: block;
  src: url("./fonts/YsabeauSC-Regular.woff") format("woff");
}
@font-face {
  font-family: "Ysabeau SC";
  font-weight: 700;
  font-style: normal;
  font-display: block;
  src: url("./fonts/YsabeauSC-Bold.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  font-display: block;
  src: url("./fonts/Lato-Regular.woff2") format("woff2");
}
/* Reset CSS */
*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

menu:not(article menu),
ol:not(article ol),
ul:not(article ul) {
  list-style: none;
}

menu,
ol,
ul {
  padding-left: 0;
}

article ol,
article ul {
  list-style-position: inside;
}

label,
button,
select,
summary,
[type=radio],
[type=submit],
[type=checkbox] {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  text-decoration: underline dotted;
  text-underline-offset: 8px;
  text-decoration-color: rgba(167, 116, 100, 0.5);
  text-decoration-thickness: 0.12em;
}

legend {
  padding: 0;
  display: table;
}

fieldset {
  border: 0;
  padding: 0.01em 0 0 0;
  margin: 0;
  min-width: 0;
}

details > summary {
  list-style-type: "▷  ";
}

details[open] > summary {
  list-style-type: "▽  ";
}

/* Global Styles */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  padding-inline: 15px;
}
@media (min-width: 1200px) {
  .wrapper {
    padding-inline: 60px;
  }
}

/* Intro */
.intro {
  text-align: center;
}

/* Upload Link */
.uploadLink {
  display: block;
  margin: 25px 0;
  width: fit-content;
  border: 5px solid navy;
  border-radius: 25px;
  font-size: 36px;
  line-height: 54px;
  font-weight: bold;
  padding: 30px;
  color: navy;
  text-decoration: none;
}

.color-highlight {
  color: #5762D5;
}

.polka {
  background-image: radial-gradient(rgba(9, 129, 74, 0.1) 20%, transparent 22%), radial-gradient(rgba(45, 71, 57, 0.1) 20%, transparent 22%);
  background-size: 40px 40px;
  background-position: 0 0, 20px 20px;
}

.visible-desktop {
  display: none;
}
@media (min-width: 1200px) {
  .visible-desktop {
    display: inline;
  }
}

@media (max-width: 767px) {
  ul.mobile-hidden,
  button.mobile-hidden,
  div.mobile-hidden {
    display: none;
  }
}

body.noscroll {
  overflow: hidden;
}

.hidden {
  display: none;
}

input, textarea {
  border: 2px solid rgba(167, 116, 100, 0.7);
  border-radius: 5px;
}

label.required_asterisk::after {
  content: "*";
  vertical-align: super;
  color: #ff5555;
  font-size: 10px;
}

.page {
  font-size: 14px;
  line-height: 26px;
  font-family: "IBM Plex Mono", monospace;
  margin: auto;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: auto;
  max-width: 100%;
  background-color: #e3e3e3;
  color: #A77464;
}

.page-main {
  background-color: #e3e3e3;
}

.page-heading {
  margin: 0 auto;
  padding-block: 20px;
}
@media (min-width: 768px) {
  .page-heading {
    padding-block-end: 22px;
  }
}
@media (min-width: 1200px) {
  .page-heading {
    padding-block: 35px;
  }
}
@media (min-width: 1440px) {
  .page-heading {
    padding-block: 45px;
  }
}
.page-heading__heading {
  font-family: "Lato", sans-serif;
  font-size: 22px;
  line-height: 1.5;
  width: fit-content;
  font-weight: 400;
  letter-spacing: 0.03em;
  opacity: 0.9;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-heading__heading {
    font-size: 28px;
    line-height: 1.2;
  }
}
@media (min-width: 1200px) {
  .page-heading__heading {
    font-size: 36px;
  }
}
@media (min-width: 1440px) {
  .page-heading__heading {
    font-size: 48px;
  }
}
.page-heading__subheading {
  font-size: 12px;
  line-height: 1.5;
  font-weight: 400;
  width: fit-content;
  margin-inline: auto;
  opacity: 0.9;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page-heading__subheading {
    font-size: 13px;
  }
}
@media (min-width: 1200px) {
  .page-heading__subheading {
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .page-heading__subheading {
    font-size: 28px;
  }
}

.main-nav {
  display: flex;
  flex-direction: column-reverse;
  padding-block-end: 20px;
}
@media (min-width: 768px) {
  .main-nav {
    padding-block: 50px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.main-nav__items {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 20px;
}
@media (min-width: 768px) {
  .main-nav__items {
    flex-direction: row;
    column-gap: 20px;
  }
}
@media (min-width: 1200px) {
  .main-nav__items {
    column-gap: 30px;
  }
}
@media (min-width: 1440px) {
  .main-nav__items {
    column-gap: 40px;
  }
}
.main-nav__item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Playfair Display", serif;
  font-size: 20px;
  line-height: 1.5;
  font-weight: 400;
  text-align: center;
  text-transform: lowercase;
  padding: 10px;
}
@media (min-width: 768px) {
  .main-nav__item {
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .main-nav__item {
    font-size: 26px;
    line-height: 1.3;
  }
}
@media (min-width: 1440px) {
  .main-nav__item {
    font-size: 30px;
    line-height: 1.2;
  }
}
@media (max-width: 1199px) {
  .main-nav__item--desktop {
    display: none;
  }
}
.main-nav__user-items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-block: 20px;
  padding-inline: 2%;
}
@media (min-width: 480px) {
  .main-nav__user-items {
    padding-inline: 10%;
  }
}
@media (min-width: 768px) {
  .main-nav__user-items {
    padding: 0;
    column-gap: 30px;
  }
}
@media (min-width: 1200px) {
  .main-nav__user-items {
    padding-inline-end: 30px;
  }
}
.main-nav__user-item {
  font-size: 11px;
  line-height: 1.2;
}
@media (min-width: 480px) {
  .main-nav__user-item {
    font-size: 14px;
    line-height: 1.2;
  }
}
@media (min-width: 1200px) {
  .main-nav__user-item {
    font-size: 16px;
    line-height: 1.2;
  }
}
@media (min-width: 1440px) {
  .main-nav__user-item {
    font-size: 18px;
    line-height: 1.2;
  }
}
.main-nav__user-item--highlight {
  display: block;
  color: #ffffff;
  border-radius: 5px;
  background-color: #A77464;
  padding: 10px;
}
@media (min-width: 1200px) {
  .main-nav__user-item--highlight {
    border-radius: 10px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.2;
  }
}
@media (min-width: 1440px) {
  .main-nav__user-item--highlight {
    font-size: 24px;
  }
}
.main-nav__user-item--highlight a:hover {
  text-decoration: none;
}
.main-nav__user-item--highlight:hover {
  outline: dotted 0.3em #A77464;
  outline-style: double;
  outline-color: rgba(167, 116, 100, 0.5);
}
.main-nav__switcher {
  margin-block-start: 10px;
  display: flex;
  justify-content: flex-start;
}
@media (min-width: 768px) {
  .main-nav__switcher {
    display: none;
  }
}
.main-nav__switcher-text {
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  line-height: 1.5;
  margin-inline-end: 20px;
}
.main-nav__switcher-button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__switcher-button {
    display: none;
  }
}
.main-nav__switcher-button--close {
  align-self: flex-end;
  padding: 10px;
}

.main-logo {
  display: block;
  width: 200px;
  height: 200px;
  font-family: "IBM Plex Sans";
  font-size: 37px;
}
.main-logo path {
  fill: transparent;
}
.main-logo text {
  fill: #A77464;
}

/* Photo Gallery */
.gallery {
  display: block;
  columns: 1;
  align-items: center;
}
@media (min-width: 768px) {
  .gallery {
    columns: 2;
    column-gap: 40px;
    margin-block-end: 40px;
  }
}
@media (min-width: 1200px) {
  .gallery {
    columns: 3;
  }
}
.gallery__photo {
  margin-block-end: 40px;
}
.gallery__img {
  border-radius: 5px;
  margin: 0 auto;
}
.gallery__photo:hover {
  cursor: pointer;
  transition: 1s ease-out;
  transform: scale(1.03);
}

.photo-popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(227, 227, 227, 0.97);
}
.photo-popup--visible {
  display: block;
}
.photo-popup__image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 90%;
  max-height: 90%;
}

.about-me {
  border: solid #A77464 5px;
  border-radius: 6%;
  text-align: center;
  background-color: #e3e3e3;
  position: relative;
  background-color: transparent;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .about-me {
    order: 3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px;
    padding-block-start: 80px;
    padding-block-end: 240px;
    padding-inline: 40px;
  }
}
.about-me:hover {
  transition: 1s ease-out;
  transform: scale(1.03);
}
.about-me:hover .about-me__background {
  transition: 1s ease-out;
  transform: scale(0.97);
}
.about-me__background {
  position: absolute;
  top: -20px;
  left: -20px;
  width: 110%;
  height: 110%;
  background-image: url("about-me-background.jpg");
  background-size: cover;
  background-color: #e3e3e3;
  background-blend-mode: overlay;
  z-index: -1;
}
.about-me__title {
  font-size: 40px;
  line-height: 1.2;
}
@media (min-width: 1200px) {
  .about-me__title {
    margin-block-end: 30px;
  }
}
.about-me__description {
  font-size: 18px;
  line-height: 1.4;
  font-weight: 700;
  text-transform: uppercase;
  margin-block-end: 5px;
}
@media (min-width: 1200px) {
  .about-me__contacts {
    display: flex;
    justify-content: space-around;
    column-gap: 36px;
    margin-block-start: 80px;
  }
}
.about-me__link {
  display: flex;
  align-items: center;
  font-size: 18px;
  line-height: 1.4;
}
.about-me__link-icon {
  margin-inline-end: 8px;
  width: 32px;
  height: 32px;
}

.page-footer {
  margin-block-start: auto;
}
.page-footer__copyright-text {
  padding-block: 20px;
  padding-inline-start: 30px;
  font-size: 12px;
  font-family: "IBM Plex Mono";
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page-footer__copyright-text {
    font-size: 16px;
  }
}

.auth-form {
  margin-block-end: 30px;
}
@media (min-width: 768px) {
  .auth-form {
    width: 600px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0 auto;
  }
}
.auth-form__title {
  font-family: "Lato", sans-serif;
  margin-block-start: 20px;
  margin-block-end: 20px;
  font-size: 22px;
}
@media (min-width: 768px) {
  .auth-form__title {
    text-transform: uppercase;
    margin-block-start: 0;
  }
}
.auth-form__subtitle {
  margin-block-end: 10px;
}
@media (min-width: 768px) {
  .auth-form__subtitle {
    font-size: 20px;
  }
}
.auth-form__form {
  width: 300px;
}
@media (min-width: 768px) {
  .auth-form__form {
    width: 500px;
    font-size: 18px;
    margin-block-start: 20px;
  }
}
.auth-form__form .errorlist {
  color: #ff5555;
  margin-block-end: 20px;
}
.auth-form__form p {
  margin-block-end: 10px;
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .auth-form__form p {
    margin-block-end: 16px;
  }
}
.auth-form__form-input {
  margin-block-start: 10px;
  margin-block-end: 20px;
}
@media (min-width: 768px) {
  .auth-form__form-input {
    margin-block-start: 20px;
    margin-block-end: 30px;
  }
}

.galleries {
  margin-block-start: 20px;
}
@media (min-width: 768px) {
  .galleries {
    display: flex;
    flex-wrap: wrap;
  }
}
.galleries__wrapper {
  display: flex;
  flex-direction: column;
  margin-block-end: 36px;
  align-items: center;
}
@media (min-width: 768px) {
  .galleries__wrapper {
    width: 50%;
    margin-block-end: 70px;
  }
}
@media (min-width: 1200px) {
  .galleries__wrapper {
    width: 33%;
    margin-block-end: 70px;
  }
}
.galleries__image-link {
  margin-block-end: 6px;
}
@media (min-width: 768px) {
  .galleries__image-link {
    margin-block-end: 12px;
  }
}
.galleries__image {
  margin: 0 auto;
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .galleries__image {
    width: 300px;
    height: 300px;
  }
}
@media (min-width: 1200px) {
  .galleries__image {
    width: 70%;
    height: auto;
    border-radius: 10%;
  }
}
.galleries__title-link {
  font-size: 22px;
}
@media (min-width: 768px) {
  .galleries__title-link {
    font-size: 28px;
  }
}

.calendar {
  margin-block-end: 20px;
}
.calendar__form .errorlist {
  color: #ff5555;
  margin-block-end: 20px;
}
.calendar__month {
  font-size: 25px;
  margin-block-end: 10px;
}
.calendar__dates {
  width: 290px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
  margin-block-end: 26px;
}
.calendar__date {
  width: 30px;
  height: 30px;
}
.calendar__date--dummy {
  visibility: hidden;
}
.calendar__date-label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border: 2px solid rgba(167, 116, 100, 0.5);
  border-radius: 20%;
  cursor: pointer;
  background-color: #fff;
}
.calendar__date-label--busy {
  background-color: rgba(167, 116, 100, 0.3);
  color: #fff;
}
.calendar__date input[type=radio] {
  display: none;
}
.calendar__date input[type=radio]:checked + label {
  background-color: #5762D5;
  color: #fff;
  border-color: #5762D5;
}
.calendar__submit {
  margin-block-start: 16px;
}
.calendar__appointment-fields {
  width: 290px;
}
.calendar__appointment-fields p {
  margin-block-end: 10px;
  display: flex;
  flex-wrap: wrap;
  row-gap: 5px;
  justify-content: space-between;
}

.breadcrumbs__container {
  display: flex;
}